.todo-box {
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 70%;
  border-radius: 5px;
  color: #fff;
  width: 80%;
  margin: 0 auto;
  padding: 2em 1em 1em;
  background: #6f6f6f;
  opacity: 0.8;
  box-shadow: 1px 1px 3px 1px rgba(169,169,169,0.651);
}
.todo-input input[type=text] {
  color: #191919;
  font-size: 1.2em;
}
.todo-list {
  flex: 1;
  overflow: scroll;
  font-size: 1.2em;
  line-height: 1.8em;
  margin: 0;
  padding: 1em;
}
