.todo-item {
  list-style: none;
  display: flex;
  align-items: center;
  padding: 0.4em 0;
}
.todo-item-checkbox,
.todo-item label {
  cursor: pointer;
}
.todo-item input[type=checkbox] {
  width: auto;
}
.todo-item .text {
  padding: 0 0.4em;
  flex: 1;
  color: #fff;
  font-weight: normal;
  font-size: 1em;
  vertical-align: sub;
}
.pure-form input.input-line-edit {
  color: #3c3c3c;
  padding: 0.2em 0.4em;
  margin-right: 1em;
  border: 1px solid #ccc;
  border-radius: 2px;
}
