.todo-bg,
.todo-wrapper {
  height: 100%;
  width: 100%;
  position: absolute;
  padding-top: 70px;
  padding-bottom: 100px;
}
.todo-bg {
  background: url(static/img/bg.811d91.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-filter: blur(4px);
          filter: blur(4px);
  opacity: 0.6;
}
.todo-wrapper {
  display: flex;
  flex-direction: column;
}
.button-todo {
  background: #ccc;
  background: #808080;
  border: 1px solid #cecece;
  box-shadow: #000;
  box-shadow: 0 0 1px 0px rgba(202,202,202,0.812);
}
.button-todo.active {
  background: #ababab;
}
